	module.exports = {"data":{"markdownRemark":{"html":"<p>[[[nicovideo:videoid=sm1592729,alt=ビーフペッパーライスを自宅で作ってみた]]]</p>\n<p>たとえあんな事件が起きたところで、あそこの看板メニューが大好きなことには変わりないんだ！ってことでいつでもどこでも毎日3食アレを食べれるように、自分で作ってみました。</p>\n<p>テキトーに作った割に意外とうまかった！うまいんだが、本物と全く同じかと言うと、まだちょっと違う味かなぁ。まぁこればかりは何度と作っては調整していけばいいかな。</p>\n<h2>材料</h2>\n<ul>\n<li>炊いた白米 茶碗1</li>\n<li>牛肉(カレー用) 片手山盛程度</li>\n<li>コーン 大さじ山盛2程度</li>\n<li>青ネギ 1本(予め刻んでおく)</li>\n<li>バター 大さじ2</li>\n<li>おろしにんにく 大さじ1</li>\n<li>とんかつソース 大さじ1</li>\n<li>粗びき胡椒</li>\n<li>\n<p>塩</p>\n<h2>レシピ</h2>\n</li>\n</ul>\n<ol>\n<li>茶碗の中にコーンを敷き、その上にバター大さじ1を入れ、さらにおろしにんにく、ソースと重ねて入れていく</li>\n<li>鉄板を強火にかけバターの残りを乗せる(ここから超手早く！)</li>\n<li>先ほどの茶碗に中身を崩さぬよう、丁寧にライスを茶碗9分目ほどまで盛る(ふっくらというより気持ち押し込むイメージ)</li>\n<li>バターが溶けてキツネ色になり煙が出始めたら中火にする</li>\n<li>茶碗に盛ったライスを鉄板の上にひっくり返す</li>\n<li>肉を鉄板にライスを囲むように置いていく</li>\n<li>ライスの上から刻みネギをぶちまける</li>\n<li>塩コショウをまんべんなく振りかける(気持ち多めに)</li>\n<li>火から下ろし、鉄板のまま食卓へ持っていく</li>\n<li>音がやむまでかき混ぜまくって食う</li>\n</ol>","frontmatter":{"cover":null,"date":"2007-11-19T00:00:00+09:00","strDate":"2007/11/18","title":"ビーフペッパーライスを自宅で作ってみた"}}},"pathContext":{"path":"/ja/blog/2007/11/19-ビーフペッパーライスを自宅で作ってみた/","langKey":"ja"}}

